        :desc="course.desc"
        :length="course.length"
        :imgSrc="course.imgSrc"
      ></sj-tile>
    </div>
    <div class="tiles__wrapper" v-else>
      <sj-custom-tile
        :key="i"
        v-for="(course, i) in filteredCourses"
        :title="course.title"
        :desc="course.desc"
        :length="course.length"
        :imgSrc="course.imgSrc"
        :templateStr="templateStr"
        :cssStr="cssStr"
      ></sj-custom-tile>
