export default {
  name: "SjTiles",
  components: {
    "sj-filters": () => import("@/components/Filters.vue"),
    "sj-tile": () => import("@/components/Tile.vue"),
    "sj-custom-tile": () => import("@/components/CustomTile.vue"),
    "sj-tile-style": () => import("@/components/TileStyle.vue"),
    "sj-searchbox": () => import("@/components/Searchbox.vue"),
  },
  methods: {
    updateFilters(filters) {
      this.filteredCourses = [];
      if (filters.length === 0) {
        this.filteredCourses = this.courses;
      }

      for (let course of this.courses) {
        if (filters.includes(course.progress)) {
          this.filteredCourses.push(course);
        }
      }

      this.tempFilteredCourses = this.filteredCourses;

      this.updateSearch();
    },
    updateSearch() {
      if (this.searchTerm === "") {
        this.filteredCourses = this.tempFilteredCourses;
        return;
      }

      this.filteredCourses = [];
      for (let course of this.tempFilteredCourses) {
        if (course.title.includes(this.searchTerm) || course.desc.includes(this.searchTerm)) {
          this.filteredCourses.push(course);
        }
      }
    },
  },
  watch: {
    searchTerm() {
        this.updateSearch();
    }
  },
  data: () => {
    return {
      searchTerm: '',
      courses: courses,
      filteredCourses: courses,
      tempFilteredCourses: courses,
      useCustomTile: false,
      templateStr: `<div class="custom-card">
