<template>
    <div class="searchbox">
        <input v-model="internalValue" @input="updateInput" class="searchbox__input" placeholder="Search!">
    </div>
</template>

<script>
  export default {
    name: "SjSearchbox",
    props: {
      value: {
        type: String,
        default: '',
      }
    },
    data() {
      return {
        internalValue: this.value,
      }
    },
    methods: {
      updateInput(event) {
        this.$emit('update:value', this.internalValue);
      }
    }
  }
</script>

<style scoped lang="scss">
.searchbox {
    &__input {
        padding: 5px;
        margin-right: 10px;
        font-size: 14px;
        &:focus {
            outline-color: hotpink;
        }
    }
}
</style>